import Vue from 'vue'
import App from './App'
import router from './router'
Vue.config.debug = true
import swipe from './assets/js/swipe'
let swiper = new swipe()
Vue.prototype.$swipe = swiper
let $root = null
Vue.prototype.$Cov = {
    setRoot(vm) {
        $root = vm
    },
    hideGlobalNav() {
        $root.state.globalNavShow = false
    },
    showGlobalNav() {
        $root.state.globalNavShow = true
    }
}
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})


