<style>
    @import './assets/css/aui.css';
    @import './assets/css/px2rem.scss'; //The first step px2rem import first
    $designWidth: 640; //Write SCSS before, must first define designWidth.
    .router-link-active{
        color: #0062cc;
    }
 .slide-left-enter-active, .slide-left-leave-active {
        transition: transform .5s;
        transform-origin: left;
    }
    .slide-left-enter, .slide-left-leave-active {
        transform: scale(0,1);
    }
    .slide-left-enter-active {
    transition-delay: 0.5s;
}
</style>
<template>
<div class='app'>
<transition name="slide-left">
<router-view></router-view>
</transition>
    <footer class="aui-bar aui-bar-tab" id="footer">
        <router-link class="aui-bar-tab-item" to="/message">
            <i class="aui-iconfont aui-icon-home"></i>
            <div class="aui-bar-tab-label">首页</div>
       </router-link>
    
        <router-link class="aui-bar-tab-item" tapmode to="/shopping">
    
            <i class="aui-iconfont aui-icon-star"></i>
    
            <div class="aui-bar-tab-label">收藏</div>
    
        </router-link>
    
        <router-link class="aui-bar-tab-item" tapmode to="/my">
    
            <div class="aui-badge">99</div>
    
            <i class="aui-iconfont aui-icon-cart"></i>
    
            <div class="aui-bar-tab-label">购物车</div>
    
        </router-link>
    
        <router-link class="aui-bar-tab-item" tapmode to="/left">
    
            <div class="aui-dot"></div>
    
            <i class="aui-iconfont aui-icon-my"></i>
            <div class="aui-bar-tab-label">我的</div>
        </router-link>
    </footer>
    </div>
</template>
<script>
require('./assets/js/hotcss.js')
</script>



